'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property' ;


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name formatInterpolate
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/string/base/format-interpolate}
*/
import formatInterpolate from '@stdlib/string-base-format-interpolate';
setReadOnly( ns, 'formatInterpolate', formatInterpolate );

/**
* @name formatTokenize
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/string/base/format-tokenize}
*/
import formatTokenize from '@stdlib/string-base-format-tokenize';
setReadOnly( ns, 'formatTokenize', formatTokenize );


// EXPORTS //

export default ns;
